import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/page/Home'
import Index from '@/components/page/Index'
import Video from '@/components/page/Video'
import Subscribe from '@/components/page/Subscribe'
import User from '@/components/page/User'
import Search from '@/components/page/Search'
import Login from '@/components/page/Login'
import Register from '@/components/page/Register'
import List from '@/components/page/List'
import typenews from '@/components/page/typenews'

Vue.use(Router)

export default new Router({
  routes: [
		{
			path: '/',
			redirect:{'name':'Home'}
		},
    {
      path: '/Home',
      name: 'Home',
      component: Home,
					redirect:{'name':'Index'},
					children:[
						{
							path:'/Home/Index',
							name:'Index',
							component:Index,
									redirect:{"name":"list"},
									children:[
										{
											path:'/Home/Index/list',
											name:'list',
											component:List
										},
										{
											path:'/Home/Index/:type',
											name:'typenews',
											component:typenews
										}									
									]
						},
						{
							path:'/Home/video',
							name:'video',
							component:Video
						},
						{
							path:'/Home/Subscribe',
							name:'Subscribe',
							component:Subscribe
						}
					]
    },
		{
			path:"/User",
			name:"User",
			component:User
		},
		{
			path:"/Search",
			component:Search
		},
		{
			path:'/Login',
			name:'Login',
			component:Login
		},
		{
			path:'/Register',
			name:'Register',
			component:Register
		}
  ]
})



