<template>
	<div>

		<mt-search v-model="value"  cancel-text="取消"  placeholder="搜索"></mt-search>
	
		<mt-button type="default" @click="tankuang">default</mt-button>
		<mt-button type="primary" @click="guanbi">primary</mt-button>
		<mt-button type="danger" @click="jiazai">danger</mt-button>
		
	</div>
</template>

<script>	
// import { Search } from 'mint-ui';
import {Toast, Indicator} from 'mint-ui';

	export default{
		
		data(){
			return{
				value:'',
				result:'',
				T:''
			}
		},
		methods:{
			tankuang(){
				Toast({
				  
				  position: 'bottom',
				  duration: 1111
				})
			},
			
			jiazai(){
				console.log("qqqqq"),
				Indicator.close()
			},
			guanbi(){
				Indicator.open({
					text: '加载中...',
					spinnerType: 'snake'
				})
			}
		}
	}
</script>


