import Vue from 'vue';

Vue.filter("filterTime",(value)=>{
	var d = new Date(value);
	return (d.getMonth()+1)+"月"+d.getDate()+"日"+d.getHours().toString().padStart(2,"0")+"时"
	+d.getMinutes().toString().padStart(2,"0")+"分"+d.getSeconds().toString().padStart(2,"0")+"秒"
})


