<template>
<div>
	<div class="swiper-container">
    <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="item in news">
			<img :src="item.thumb" />
        </div>     
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination"></div>
		</div>
		<p v-for="item in news">{{item.fpTime|filterTime}}</p>
     <ul  v-infinite-scroll="loadMore"
     		infinite-scroll-disabled="loading"
     		infinite-scroll-distance="10">
		<li v-for="item in list" class="row">
			<div> <h3>{{item.companyFullName}}222</h3>
					<p>{{item.positionName}}111 </p>
				 <p>{{item.createTime}}</p> 
			</div>
			<div>
				<img :src="`http://www.lgstatic.com/`+item.companyLogo" width="100"/>
			</div>
		</li>
     </ul>
</div>
</template>
<script>
	import {mapActions,mapState} from "vuex"
	import "../../../node_modules/swiper/dist/css/swiper.css";
    import Swiper from 'swiper';		
		export default {
			data(){
				return {
					news:[]   //2.拿到数据放在news里		
				}
			 },
			created(){
			this.$jsonp("http://cre.mix.sina.com.cn/api/v3/get?_=1543803494741").then((res)=>{
   	  			this.news = res.data.filter((item,index)=>{  //1.	取数据
   	  			 	return index<5;
   	  			 })
						 console.log(this.news)
   	  		})
				this.getData();
			},
			computed:{
				...mapState(["list"])
			},
			methods:{
				loadMore(){
					 this.getData();
				},
				...mapActions(["getData"])
			   },
			watch:{
				news(){   //3 监听数据的变化
					this.$nextTick(()=>{   //4 等待dom更新完成
						  var mySwiper = new Swiper ('.swiper-container', {  //5实例化
								loop: true, // 循环模式选项
								// 如果需要分页器
								pagination: {
								  el: '.swiper-pagination',
								},
								autoplay: {
									delay: 3000,
									disableOnInteraction: false //false 用户操作后还能自动播放
								}
						 })   
					})
				 }
			}
}
</script>

<style lang="scss">
    $sc:25;	
	.swiper-wrapper img{
		width: 100%;
	height: 135/$sc+rem;
	}	
	.row{
		display: flex;
		padding: 0.34rem 0.48rem 0.16rem;
    width: 100%;
    border-bottom: 1px solid #dbdbdb;
    background: #f0f0f0;
		img{
			width:110/$sc+rem;
		}
	}
</style>



