<template>
	<div>
		<router-link to="/user">返回</router-link>
		<router-link to="/Register" class="reg">注册</router-link><br>
		<input type="text" name="uname" v-model="user" placeholder='手机/邮箱'><br>
		<input type="password" name="password" v-model="pwd" placeholder="密码"><br>
		<button @click="login">登录</button><br>
<!-- 		<button @click="getdata">获取</button><br> -->
		<span v-for="item in list">手机号{{item.userphone}}<br>密码{{item.userpwd}}</span>
	</div>
</template>

<style>
	.reg{
		float:right;
	}
</style>
<script>
//	import axios from 'axios'
	export default{
		data(){
			return{
				user:'',
				pwd:'',
				list:[]
			}
		},
		methods:{
			login(){
				this.$http.get("http://localhost:3000/list?userphone="+this.user).then((res)=>{
					console.log(res.data)
					if(this.isphone()){
						if(res.data.length!=0){
								if(res.data[0].userpwd===this.pwd){
									this.$router.push("/Home/Index")
								}else{
									alert("密码错误")
								}														
						}else{
							alert("用户名不存在")
						}
					}				
				})
			},
			isphone(){
				var myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
				if (!myreg.test(this.user)) {
					alert("用户格式不对")
				} else {
					return 1;
				}
			}		
		}	
	}
</script>


