import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);
import { Toast } from 'mint-ui';
import axios from 'axios'


var store = new Vuex.Store({
	state:{							//2.拿到数据放在news里
		list:[],	
		page:1,
		loading:false
	},
	mutations:{
		moreList(state,newlist){
			state.list = state.list.concat(newlist)
			
		},
		changeLoading(state,p){
			state.loading = p
		},
		changePage(state,p){
			state.page = p
		}
	},
	actions:{
		 getData(context){
		 		if(context.state.page===8){
		 				Toast({
		 				message: '没有更多数据了',
		 				position: 'bottom',
		 				duration: 1500
		 			});
		 				return;
		 		}
				context.commit("changeLoading",true)
		 		var t= Toast({
		 				iconClass: 'fa fa-spinner fa-pulse',
		 				position: 'middle',
		 				duration: -1
		 		});
		 		axios.get(`/lg/listmore.json`,{
		 			params:{
		 				pageNo:context.state.page,
		 				pageSize:15
		 			}
		 		}).then((res)=>{
		 			context.commit("moreList",res.data.content.data.page.result);
		 			context.commit("changePage")
		 			context.commit("changeLoading",false)
		 			t.close();
		 		})
		 	}
	}
})

export default store;



