<template>
	<div>
		<header><div class="top"><i class="fa fa-volume-up left"></i><img class="dog" style="-webkit-user-select: none;max-width: 100%;" src="http://106.12.193.193:3000/img/top-logo.png"><router-link to="/search">
		<i class="fa fa-search right"></i></router-link>
		</div>
		<Nav class="nav"/></header>
		<router-view class="content"/>
		<footer>
			<ul>
				<li><router-link to="/home/index">首页</router-link></li>
				<li><router-link to="/home/video">视频</router-link></li>
				<li><router-link to="/home/subscribe">订阅</router-link></li>
				<li><router-link to="/user">个人</router-link></li>					
			</ul>
		</footer>
	</div>
</template>
<script>
	import  '../../style/reset.css';
	import Nav from "./Nav"
	export default {
	components:{
			Nav
	}
	}
</script>
<style lang = 'scss'>
	@import "../../style/reset.css"; 
	@import "../../style/var.scss";
	body{
		font-size: 0.56rem;
	}
	header{
		width:100%;
		height:75/$sc+rem;
		background: #ccc;
		position:fixed;
		top:0;
		left:0;
		font-size: 0.56rem;
		.top{
			display: flex;
		}
		.top{
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
		.dog{
			height:0.94rem;
			width:107/$sc+rem;
			margin:0;
			padding:0;
			box-sizing: border-box;
		}
		.left{
			margin:10/$sc+rem 0 0 10/$sc+rem;
		}
		.right{
			float:right;
			margin:10/$sc+rem 10/$sc+rem 0 0;
		}
		.nav{
			margin-top: 1rem;
		/* 	margin-bottom:0rem; */
		}
	}
	.content{
		position: fixed;
		top:75/$sc+rem;
		bottom:49/$sc+rem;
		left:0;
		right:0;
		background:#9db3bd;
		overflow-y: auto;
	}
	footer{
		width:100%;
		height:49/$sc+rem;
		background: #CACACA;
		position:fixed;
		bottom:0;
		left:0;
		font-size: 0.56rem;
		ul{
			display:flex;
			justify-content: center;
			align-content: center;
			li{
				flex:1;
				text-align: center;
				height:49/$sc+rem;		
				padding-top: 0.5+rem;
				a{
					line-height: 49$sc+rem;
				}
			}
		}
	}
</style>


