import Vue from 'vue';
Vue.directive("scroll",(el,binding)=>{
		var dis = Number(el.getAttribute('dis'));
	el.addEventListener("scroll",()=>{
		if(el.scrollTop > dis){
			binding.value.flag= true;
		}else{
			binding.value.flag = false;
		}
	})
})

Vue.directive("top",(el,binding)=>{
	el.onclick = function(){
		el.parentNode.scrollTop = 0
	}
})





// import Vue from 'vue';
Vue.directive("gd",{//el 指令绑定的元素  binding 指令的值  只读
	bind(el){
	var dis = Number(el.getAttribute('dis'));
	var timer = null;
	var flag = false;
	el.addEventListener('scroll',()=>{
		if(el.scrollTop>dis){
			if(flag) return;
			console.log("scroll");
			flag = true;
			if(timer) clearInterval(timer);
			timer = setTimeout(()=>{
				console.log("计算器")
			},1000)
		}
	})
	}
})



