<template>
<div ref="list" class="list" v-scroll="vis" v-gd dis="300">
     <ul>
		<li v-for="item in news" class="row">
			<div>
				<h2>{{item.mintro}}</h2>
			<p>{{item.media}}</p>
			</div>
			
			<div>
				<img :src="item.thumb" width="100" />
			</div>
		</li>
     </ul>
		 <button v-top class="btn" v-show="vis.flag">返回顶部</button>
		 <transition name="scroll" />
</div>
</template>

<script>
   import "../../../node_modules/swiper/dist/css/swiper.css"
    import Swiper from 'swiper'
export default {
	data(){
      	return {
      		news:[],   //2.拿到数据放在news里
      		type:'',
      		vis:{
						flag:false
					}
      	}
      }, 
     watch:{
   	  	$route:{
   	  		handler(n){
   	  			this.type=n.params.type;
   	  			this.getData();
   	  		},
					
					immediate:true
   	  	}
   	  },
   	  methods:{
   	  	getData(){
   	  		 this.$jsonp(`http://cre.mix.sina.com.cn/api/v3/get?cateid=1&cre=tianyi&mod=${this.type}&merge=3&statics=1&length=20&up=1&down=0&fields=media&_=1543809052894`).then((res)=>{
   	  			console.log( res)
   	  			this.news = res.data.filter((item,index)=>{  //1.	取数据
   	  			 	return index<15;
   	  			 })
   	  			//console.log(res.data.data instanceof Array)
   	  		})
   	  	}
   	  }

}

</script>

<style lang="scss">
    $sc:25;
	
	.swiper-wrapper img{
		width: 100%;
	height: 135/$sc+rem;
	}	

	.row{
		display: flex;
		padding: 0.34rem 0.48rem 0.16rem;
		width: 100%;
		border-bottom: 1px solid #dbdbdb;
		background: #f0f0f0;
		img{
			width:110/$sc+rem;
		}
	}
	
	.list{
		position: fixed;
		top:75/$sc+rem;
		bottom:49/$sc+rem;
		left:0;
		right:0;
		overflow: scroll;
	}
	.btn{
		position: fixed;
		z-index: 999;
		width:50/$sc+rem;
		height:50/$sc+rem;
		background: #6994a9;
		border-radius: 50%;
		bottom:100/$sc+rem;
		left:300/$sc+rem;
	}
</style>


