<template>
	<div>
		<div id="nav">
			<div id="leftnav" class="leftnav">
				<div class="swiper-container2">
					<div class="swiper-wrapper w">
						<div class="swiper-slide" v-for="(item,index) in titles"
						:class="{'active':curIndex===index}">
						{{item.text}}
						</div>
					</div>
				</div>
			</div>
			<div><i class="fa fa-navicon"></i></div>
		</div>
	</div>
</template>
<script>
	import "../../../node_modules/swiper/dist/css/swiper.css";
	import Swiper from "swiper";
	export default{
		data(){
			return{
				curIndex:0,
				titles:[{
						text:'主页1',
						type:''
				},
				{
					text:'科技2',
					type:'pcent'
				},
				{
					text:'娱乐3',
					type:'pctech'
				},
				{
					text:'科技4',
					type:'kj'
				},
				{
					text:'娱乐5',
					type:'yl'
				},
				{
					text:'科技6',
					type:'kj'
				},
				{
				
					text:'科技7',
					type:'kj'
				},
				{
					text:'娱乐8',
					type:'yl'
				},
					{text:'娱乐9',
					type:'yl'
				},
				{
					text:'科技a',
					type:'kj'
				},
				{
					text:'娱乐b',
					type:'yl'
				},
				{
					text:'科技c',
					type:'kj'
				},
				{
					text:'娱乐d',
					type:'yl'
				}]
			}
		},
		mounted(){
			var vm = this;
			new Swiper(".swiper-container2",{
				freeMode:true,
				slidesPerView:'auto',
				on:{
						click(){  
							vm.curIndex = this.clickedIndex;
						//单击 当前导航条到中心位置
							this.setTransition(200);
							var el = this.slides[this.clickedIndex];  //当前单击 元素
							var elpos = el.offsetLeft + el.clientWidth/2; //当前单击 元素中心位置
								var swiperContainerWidth = this.wrapperEl.clientWidth; //容器的宽度
								var maxDis = this.maxTranslate();//最大的滚动距离
								var maxPos = -maxDis + swiperContainerWidth/2; //最大的中心位置
								if(elpos < swiperContainerWidth/2){
										this.setTranslate(0)
								}
								else if(elpos>maxPos){
									this.setTranslate(maxDis)
								}
								else{
									this.setTranslate(swiperContainerWidth/2-elpos)
								}
								if(vm.curIndex===0){
									vm.$router.push({'name':'list'})
								}
								else{
									vm.$router.push('/home/index/'+vm.titles[vm.curIndex].type)
								}
								
						}
				}
			})
		}
	}

</script>
<style lang='scss'>
	$sc:25;
	#nav {
		display: flex;
		width:100%;	
	}
	.leftnav{
		width:90%;
		overflow: hidden;
		margin-left: 10px;
		margin-bottom: 0%;
	}
	.w div{
		width:50px;
		text-align: center;
	}
	.active{
		color:red;
		border-bottom: 2px solid red;
	}
</style>



